import { React, useState } from "react";
import { useForm } from "react-hook-form";
import { ErrorMessage } from "@hookform/error-message";
import postsApi from "../../api/postsApi";
import uploadApi from "../../api/uploadApi";
import dataCity from "./local.json";
import { useNavigate } from "react-router-dom";
import "./CreatePost.scss";

function CreatePost() {
  const [sell, setSell] = useState(false);
  const [rent, setRent] = useState(false);
  const [checkDistricts, setCheckDistricts] = useState([]);
  const [checkWards, setCheckWards] = useState([]);
  const navigate = useNavigate();

  const optionSelectorArea = (e) => {
    setSell(e === "sell");
    setRent(e === "rent");
  };

  const optionSell = [
    { value: "house", title: "Nh" },
    { value: "land", title: "t" },
  ];
  const optionRent = [
    { value: "house", title: "Nh" },
    { value: "land", title: "t" },
    { value: "homestay", title: "Phng tr" },
  ];

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    reValidateMode: "onSubmit",
    defaultValues: {
      want: "",
      title: "",
      classify: "",
      location: {
        address: "",
        city: "",
        ward: "",
        district: "",
      },
      utilities: {
        acreage: "",
        price: "",
        bedroom: "",
        bathroom: "",
      },
      description: "",
    },
  });

  const onSubmit = async (data) => {
    const config = {
      headers: {
        "Content-Type": "multipart/form-data",
        Authorization: localStorage.getItem("token"),
      },
    };
    let formData = new FormData();
    const files = data.files;
    for (let i = 0; i < files.length; i++) {
      let file = files.item(i);
      const s = `photo`;
      formData.append(s, file);
      console.log(file);
      console.log(formData);
    }
    await uploadApi
      .upload(formData, config)
      .then((response) => {
        const photos = response.photos;
        const postData = { ...data, photos };
        postsApi.create(postData, config).then((res) => {
          navigate(`/${res.slug}`);
        });
      })
      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <div style={{ minHeight: "100vh" }} className="container">
      <div className="row pt-3">
        <h1 className="text-center">ng bi</h1>
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="row">
            <div className="col-12 col-md-6 pb-3">
              <span className="title-input ">Bn mun: </span>
              <input
                {...register("want", { required: true })}
                type="radio"
                onChange={(e) => optionSelectorArea("sell")}
                className="btn-check r-sell"
                defaultValue="sell"
                id="option1"
              />
              <label
                className="btn btn-custom_l form-control-lg"
                htmlFor="option1"
              >
                Bn
              </label>
              <input
                {...register("want", { required: true })}
                type="radio"
                onChange={(e) => optionSelectorArea("rent")}
                className="btn-check r-rent"
                defaultValue="rent"
                id="option2"
              />
              <label
                className="btn btn-custom_r form-control-lg"
                htmlFor="option2"
              >
                Cho thu
              </label>
            </div>
            <div className="col-12 col-md -6">
              <select
                type="select"
                Disable={!sell && !rent ? true : false}
                className="form-select"
                {...register("classify", { required: true })}
              >
                <option disabled selected>
                  Chn bt ng sn
                </option>
                {sell
                  ? optionSell.map((data) => (
                      <option value={data.value}>{data.title}</option>
                    ))
                  : null}
                {rent
                  ? optionRent.map((data) => (
                      <option value={data.value}>{data.title}</option>
                    ))
                  : null}
              </select>
            </div>
            <div className="mt-3">
              <label htmlFor="exampleInput" className="form-label">
                Tiu  bi vit:
              </label>
              <input
                className="form-control form-control-lg"
                type="text"
                {...register("title", { required: true })}
                placeholder="Tiu  ca bn"
              ></input>
            </div>
            <div className="mt-3">
              <label htmlFor="exampleInput" className="form-label">
                a ch:
              </label>
              <input
                className="form-control"
                type="text"
                {...register("location.address", { required: true })}
                placeholder="(v tr khu t, nh, phng tr...)"
              ></input>
            </div>
            <div className="row mt-3">
              <div className="col-12 col-sm-4">
                <label htmlFor="exampleInput" className="form-label">
                  Thnh ph/Tnh:
                </label>
                <select
                  Disable={!sell && !rent ? true : false}
                  className="form-select"
                  {...register("location.city", { required: true })}
                  onChange={(e) => {
                    const districtsOfLand = dataCity.find(
                      (data) => data.name === e.target.value
                    );
                    setCheckDistricts(districtsOfLand.districts);
                  }}
                >
                  <option disabled selected>
                    Thnh ph/Tnh
                  </option>
                  {dataCity.map((city) => (
                    <option value={city?.name}>{city?.name}</option>
                  ))}
                </select>
              </div>
              <div className="col-12 col-sm-4">
                <label htmlFor="exampleInput" className="form-label">
                  Qun/Huyn:
                </label>
                <select
                  Disable={!sell && !rent ? true : false}
                  className="form-select"
                  {...register("location.district", { required: true })}
                  onChange={(e) => {
                    const wardsOfLand = checkDistricts.find(
                      (data) => data.name === e.target.value
                    );
                    setCheckWards(wardsOfLand.wards);
                  }}
                >
                  <option disabled selected>
                    Qun/Huyn
                  </option>
                  {checkDistricts?.map((dist) => (
                    <option value={dist?.name}>{dist?.name}</option>
                  ))}
                </select>
              </div>
              <div className="col-12 col-sm-4">
                <label htmlFor="exampleInput" className="form-label">
                  Phng/X:
                </label>
                <select
                  Disable={!sell && !rent ? true : false}
                  className="form-select"
                  {...register("location.ward", { required: true })}
                >
                  <option disabled selected>
                    Phng/X
                  </option>
                  {checkWards.map((ward) => (
                    <option value={ward.name}>{ward.name}</option>
                  ))}
                </select>
              </div>
            </div>
            <div className="row mt-3">
              <div className="col-6 col-sm-3">
                <label htmlFor="inputPassword" className="col-form-label">
                  Din tch
                </label>
                <div className="input-group">
                  <input
                    type="text"
                    className="form-control"
                    {...register("utilities.acreage", { required: true })}
                  />
                  <span className="input-group-text">m</span>
                </div>
              </div>
              <div className="col-6 col-sm-3">
                <label htmlFor="inputPassword" className="col-form-label">
                  Gi c:
                </label>
                <div className="input-group">
                  <input
                    type="text"
                    className="form-control"
                    {...register("utilities.price", { required: true })}
                  />
                  <select className="input-group-text">
                    <option value="1">Triu</option>
                    <option value="2">T</option>
                  </select>
                </div>
              </div>
              <div className="col-6 col-sm-3">
                <label for="inputPassword" className="col-form-label">
                  Phng ng
                </label>
                <div className="input-group">
                  <input
                    type="text"
                    className="form-control"
                    {...register("utilities.bedroom")}
                  />
                  <span className="input-group-text">m</span>
                </div>
              </div>
              <div className="col-6 col-sm-3">
                <label htmlFor="inputPassword" className="col-form-label">
                  Phng tm
                </label>
                <div className="input-group">
                  <input
                    type="text"
                    className="form-control"
                    {...register("utilities.bathroom")}
                  />
                  <span className="input-group-text">m</span>
                </div>
              </div>
            </div>

            <label for="exampleInput" className="col-sm-1 col-form-label">
              M t
            </label>
            <div className="input-group pt-3">
              <textarea
                for="exampleInput"
                className="form-control"
                {...register("description", { required: true })}
              ></textarea>
            </div>
            <div className="pt-3">
              <label htmlFor="formFileLg" className="form-label">
                Chn nh
              </label>
              <input
                className="form-control form-control-lg"
                id="formFileLg"
                {...register("files")}
                type="file"
                multiple
              />
            </div>
          </div>
          <div className="text-center mt-3">
            <button type="submit" className="btn btn-primary btn-lg ">
              ng bi
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default CreatePost;
