import { React } from "react";
import { useForm } from "react-hook-form";
import usersApi from "../../api/usersApi";
import { ErrorMessage } from "@hookform/error-message";

function SignUp() {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    defaultValues: {
      name: "",
      email: "",
      password: "",
      telephone: "",
      role: "mod",
    },
  });

  const onSubmit = async (data) => {
    await usersApi
      .register(data)
      .then((response) => {
        console.log(response);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <div>
      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="divider d-flex align-items-center my-4">
          <p className="text-center fw-bold mx-3 mb-0">To ti khon</p>
        </div>
        <div className="form-outline mb-4">
          <input
            type="text"
            {...register("name")}
            className="form-control form-control-lg"
            placeholder="H v tn"
          />
          <label className="form-label">H v tn</label>
        </div>
        <div className="form-outline mb-4">
          <input
            type="text"
            {...register("telephone")}
            className="form-control form-control-lg"
            placeholder="S in thoi"
          />
          <label className="form-label">S in thoi</label>
          <ErrorMessage
            errors={errors}
            name="telephone"
            render={({ message }) => <p className="text-danger">{message}</p>}
          ></ErrorMessage>
        </div>
        <div className="form-outline mb-4">
          <input
            type="email"
            {...register("email")}
            className="form-control form-control-lg"
            placeholder="in ti khon"
          />
          <label className="form-label">Email</label>
        </div>
        <div className="form-outline mb-3">
          <input
            type="password"
            autoComplete="true"
            {...register("password", { required: true })}
            className="form-control form-control-lg"
            placeholder="in mt khu"
          />
          <label className="form-label">Mt khu</label>
        </div>
        <div className="form-outline mb-3">
          <select
            type="select"
            {...register("role", { required: true })}
            className="form-select form-control-lg"
          >
            <option value="mod">Qun tr vin</option>
            <option value="admin">Qun l (Cp bc cao nht)</option>
          </select>
          <label className="form-label">Vai tr</label>
        </div>
        <div className="text-center mt-4 pt-2">
          <button type="submit" className="btn btn-primary btn-lg">
            To ti khon
          </button>
        </div>
      </form>
    </div>
  );
}

export default SignUp;
